.movie {
    width: 155px;
    height: 280px;
    border-radius: 10px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 70px;
    background-color: white;
    box-shadow: 0 50px 100px -20px rgb(50, 50, 93, 0.25),
     0 30px 60px -30px rgb(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie figure {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.movie .movie__cover {
    position: relative;
    border-radius: 10px;
    top: -20px;
    max-width: 150px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
     0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie figcaption {
    opacity: .0;
    overflow: hidden;
    height: 180px;
}

.movie figure:hover > .movie__cover {
    top: -10px;
    opacity: 0.2;
    cursor: pointer;
    transition: all .1s linear;
}

.movie figure:hover > figcaption {
    opacity: .8;
    cursor: pointer;
    transition: all .2s linear;
}

.movie figure:active > .movie__cover {
    transform: scale(1.05);
    transition: all .05s linear;
}

.movie .movie__caption {
    position: absolute;
    text-align: center;
    color: #000;
}

.movie .movie__data {
    position: relative;
    top: -30px;
    padding: 5px;
}

.movie .movie__title {
    display: block;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    text-overflow: ellipsis;
    text-decoration: none;
    overflow: hidden;
    white-space: nowrap;
    color: #2c2c2c;
}

.movie .movie__year {
    margin: 0;
    font-weight: 300;
    color: #adaeb9;
}

.movie .movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie__genres li,
.movie .movie__year {
    margin-right: 10px;
    font-size: 14px;
}
