.detail {
    position: fixed;
    overflow: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    border-radius: 20px;
    top: 10%;
    width: 80vw;
    height: 80%;
    z-index: 1;
    background-color:#eff3f7;
    background-color: rgba(239,243,247,0.9);
}

.detail::-webkit-scrollbar {
    display: none;
}

.detail__close {
    float: right;
    margin-top: 15px;
    margin-right: 30px;
    color: rgba(0,0,0,0.5);
}

.detail__close:hover {
    cursor: pointer;
}

.detail__content {
    display: block;
    text-align: center;
    padding: 50px;
    font-size: 18px;
    font-weight: 300;
}

.detail__content h6 {
    font-size: 17px;
    font-weight: 350;
}

.detail__content img {
    border-radius: 10px;
    max-width: 300px;
}

.detail__video {
    border-radius: 10px;
    width: 100%;
    height: 40vw;
    border: 0;
}