.app {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.title {
    padding-top: 20px;
    font-weight: 300;
    text-align: center;
}

.combobox {
    display: flex;
    justify-content: flex-end;
}

.container {
    display: flex;
    height: 100%;
    justify-content: center;
}

.loader {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: flex;
    justify-content: left;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    width: 80%;
}